.img {
  width: 270px;
  height: 100%;
}

.imgWrap {
  display: flex;
  margin-left: 100px;
  margin-top: 30px;
}

.descrWrap {
  display: inline-block;
  margin-left: 50px;
  padding-right: 100px;
}

.btn {
  padding-left: 25px;
  color: #272727;
  font-size: 16px;
  border: none;
  background-color: #fff;
  border-radius: 7px;
  margin-left: 5px;
}

.btn:hover {
  color: #e0670b;
  cursor: pointer;
}

.btnList {
  list-style: none;
  display: flex;
  margin-left: 76px;
}

.castBtn {
  padding: 9px 9px 9px 25px;
  position: relative;
  background-color: #e0670b;
  font-size: 16px;
  border: none;
  width: 110px;
  color: #fff;
  border-radius: 7px;
}

.castBtn:hover {
  background-color: #126b18dc;
  cursor: pointer;
}

.reviewsBtn {
  position: relative;
  padding: 9px 9px 9px 30px;
  background-color: #e0670b;
  font-size: 16px;
  width: 110px;
  border: none;
  color: #fff;
  border-radius: 7px;
  margin-left: 15px;
}

.reviewsBtn:hover {
  background-color: #126b18dc;
  cursor: pointer;
}

.descrTitle {
  font-size: 20px;
  font-weight: 600;
}

.loading {
  display: flex;
  width: 100%;
  height: 100vh;
  background: #7400bc29;
  position: absolute;
  transition: 0.5s;
  top: 0;
  left: 0;
}
